body{
   font-family: 'Roboto', sans-serif;;
}
.search_container {
  background-image: url("../src/assets/background.jpg");
   width: 100%;
  height: 800px;
  display: flex;
  flex-direction: row;
}
@media only screen and (min-device-width: 480px) {
  .search_container {
    width: 544px;
  }
}

.search_container div {
  width: 100%;
  
}
.current_container{

  height: 800px;
  display: flex;
  flex-direction: row;
  padding: 25px;
  background-color : #e7e5e5;
}
@media only screen and (min-device-width: 480px) {
  .current_container{
    width: 544px;
  }
}
.cont{
  margin: 30% auto;
}
.text {
  display: flex;
  margin: 10px;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  color: aliceblue;
  font-weight: 900;
}
@media only screen and (min-device-width: 480px) {
  .text {
    
    font-size: 30px;
   
  }
}
.search_city {
  margin: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-color: green;
}
.nav_container{
  margin-top: 20px;
  margin-bottom: 10px;
}
.nav_text{
  font-size: 20px;
  font-weight: 800;
}
.search_city input {
  height: 40px;
  width: 90% !important;
  border-radius: 5px;
  font-size: 20px;
  font-weight: 500;
  border-color: green;
}
.get_button {
  margin: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.get_button button {
  border-radius: 5px;
  background-color: green;
  color: white;
  height: 40px;
  width: 90% !important;
  font-weight: 500;
  font-size: 20px;
}
.weather_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 800px;
}
.current_container{
  display: flex;
  flex-direction: column;
 
}
.city_txt{
   font-size: 25px;
   font-weight: 700;
}
.city_cont{
  display: flex;
  flex-direction: column;
  
}
.weather_cont{
  display: flex;
  flex-direction: row;
  flex: 1 1 1;
  margin-bottom: 20px;
  margin-top: 30px;
}
.temp_cont{
  display: flex;
  flex-direction: column;
}
.weather_img{
   width: 50%;
}
.weather_img img{
  width: 80%;
}
.report{
  width: 50%;
  background-color: rgb(173, 170, 170);
  border-radius: 5px;
  font-size: 25px;
  font-weight: 700;
  padding: 25px;
}
.temp_txt{
  font-size: 80px;
  font-weight: 900;
}
.desc{
  font-size: 25px;
   font-weight: 700;
   color: blue
}
.date_cont{
  color:  rgb(173, 170, 170);
  font-weight: 700;
  padding-top: 10px;
  text-transform: uppercase;
}
.nav_container div{
  display: flex;
  
  justify-content: space-between;
  align-items: center;
}
@media only screen and (min-device-width: 480px) {
  .nav_container div{
    width: 544px;
  }
}

.nav_container div a{
   cursor: pointer;
   color: green;
   padding: 0px 10px;
}
.nav_text{
  justify-content: center !important;
}
.nav_text img{
  width: 40px;
  height: 40px;
  margin-top: 10px;
}

.forecasts_container{
  
  height: 800px;
  display: flex;
  flex-direction: column;
  background-color : #e7e5e5;
}
@media only screen and (min-device-width: 480px) {
  .forecasts_container{
    height: 800px;
  width: 544px;
  }
}
.forecast_container{
  display: flex;
  padding: 10px;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  border: 2px solid gray;
  border-bottom: none;
}

.img_forcast img{
  width: 150px;
  height: 150px;
}
.foreCast_temp{
  font-size: 25px;
  font-weight: 700;
  text-transform: uppercase;
}
.nav_text{
  color: green;
}

